var _self;
export default {
	data() {
		return {
			name: '',
			url: ''
		}
	},
	onLoad(options) {
		_self = this;
		_self.name = options.name;
		_self.url = options.url;
		uni.setNavigationBarTitle({
			title: _self.name,
		});
		uni.showTabBar({
			animation:true
		});
		uni.setTabBarItem({
			index:0,
			text:'text'
		});
		uni.setTabBarStyle({
		  color: '#FF0000',
		  selectedColor: '#00FF00',
		  backgroundColor: '#0000FF',
		  borderStyle: 'black'
		})
	},
	methods: {
		record(){
			uni.switchTab({
				url:"../record/record",
			})
		}
	}
}
